.cardContainer{
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
width: 150px;
height: 200px;
background-color: aliceblue;
cursor: pointer;
}

.cardContainer:hover{
filter: drop-shadow(5px 5px 5px black);
background-color: bisque;
}

.titleCard{
    font-size: 1.2rem;
    text-align: justify;
    color:violet;

}

.imgCard{
    width: 250px;
    border-radius: 6px;
    height: 280px;
}

.genreCard,
.ratingCard{
font-size: 1rem;
color:#ffffff;
text-align: center;

}