.containerForm{
    color:antiquewhite;
    width: 100vw;
    height:100vh;
    line-height:0pc;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.containerFormInside{
    width:30%;
    height:100vh;
    border-radius: 5px;
    border: 2px black;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

.textForm{
    font-size:0.8rem;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 15px;
}

.inputForm{
    width:230px;
    height:15px;
    background-color:black;
    border: 1px solid white;
    color:blueviolet;
    border-radius: 5px;
    margin-top: -5px;
}

.inputForm:focus{
    color:burlywood;
    background-color: #000532;
    outline-color: #317fb3;
}
.errorText{
    font-size: 12px;
    color:red;
    font-family: Arial, Helvetica, sans-serif;
}
.buttonBack{
    left:0;
    position:absolute;
}
.inputFormDescription{
    width: 230px;
    height:60px;
    resize:none;
    background-color:coral;
    border: 1px solid white;
    border-radius: 5px;
    margin-top: -5px;
}

.selectForm{
    width:230px;
    background-color:#317fb3;
    box-shadow: 1px 1px 2px;
    cursor:pointer;
    color:white;
    border-radius: 5px;
    border:2px solid rgb(20, 169, 206)
}
.selectForm:focus{
    border: 1px solid rgb(20, 169,206);
    outline-color: #317fb3;
}

.buttonForm{
    cursor:pointer;
    background-color: #317fb3;
    border: none;
    margin-top: 5px;
    margin-right: 5px;
    color: white;
    border-radius: 3px;
    font-family: Arial, Helvetica, sans-serif;
}

.buttonForm:hover{
    transition: all 0.3s ease-in-out;
}

.buttonCreate{
    margin-top: 10px;
    cursor: pointer;
    font-size: 15px;
    position: relative;
    font-family: initial;
    overflow: hidden;
    transition: all 0.3s;
    border: 2px solid peru;
    box-shadow: inset;
}
.ButtonBack{
cursor: pointer;
margin: 15px, 0px, 0px, 15px;
font-size: 16px;
height: 35px;
border: 0px solid;
color: white;
border-radius: 50px;
}
.containertitle{
    margin-top: 50px;
}