.containerDetail{
    display:flex;
    flex-wrap: wrap;
    height: 100vh;
    width: 99vw;
}
.containerTitle{
    height: 80px;
    position: absolute;
    margin-left: 15px;
    width: 34%;
}
.detailTitle{
    color:aliceblue;
    font-size :1.8em;
    font-family: Arial, Helvetica, sans-serif;
    text-align: justify;
}
.containerImg{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 452px;
    height: 331px;
    margin-left: 15px;
}
.imgDetail{
    width: 450px;
    height: 250px;
    margin:auto;
    border-radius: 5px;
}
.containerDescription{
    display: inline;
    width: 65%;
    height: 350px;
}
.descriptiontext{
    color: white;
    font-size: 1.05rem;
    text-align: justify;
    margin-top: 20px;
    padding: 20px;
    font-family: Arial, Helvetica, sans-serif;
}
.containerGpr{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100vw;
    height: 250px;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    font-size: 1rem;
}
.btnBack{
    margin-top: 20px;
    width: 130px;
    height: 40px;
    font-size: 1.1rem;
    cursor: pointer;
    background-color: black;
    color: whitesmoke;
    border: none;
    border-radius: 5px;
    transition: all0.4s;
}
