.container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
}

.img_background{
    background-image: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSd2U85yeEgLWKZFG9v34ExDn0x0iu8G0tULA&usqp=CAU');
    width: 100vw;
    height: 100vh;
    position:absolute;
    object-fit: cover;
    z-index: -1;
}

.buttonContainer{
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-start;
    align-items: center;
    width: 200px;
    height: 270px;
    flex-wrap: wrap;
}

.link{
    text-decoration: none;
}

.btn{
    width: 6em;
    height: 2.2em;
    background: black;
    color:white;
    border: none;
    border-radius: 0.625em;
    font-size: 20px;
    cursor: pointer;
    position: relative;
    z-index: 1;
    overflow: hidden;
    border:1px solid rgb(3, 152, 197);

};

.btn:hover{
    color: black;
}

.btn:after{
    content: "";
    background: #006f95;
    position: absolute;
    z-index: -1;
    left: -20%;
    right: -20%;
    top: 0;
    bottom: 0;
}