.input{
    background-color: black;
    width: 220px;
    height: 14px;
    padding :6px;
    text-align: center;
    border-radius: 5px;
    color: white;
}

.buttonSearch{
    background: linear-gradient(-45deg, #3f00b5, #9f69fe, #27c8b7, #3f00b5);
    background-size: 800% 400%;
    display: inline-block;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    font-size: 17px;
    font-weight: 700;
}

